.login-main-div {
	margin: auto;
	max-width: 500px;
	margin-top: 100px;
}

.input-form-login {
	margin: auto;
	padding: 20px;
	height: 220px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.title-div {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.error {
	padding: 0;
	margin: 0;
	color: red;
	padding-right: 20px;
}
.title-login {
	padding-left: 20px;
	font-size: 1.5em;
	font-weight: 600;
}

.MuiButton-label {
	height: 25px;
}
